.contacts {
  margin: 0;

  &--gray {
    background-color: $bggray
  }
}

.contacts__text-block {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  padding: 0 20px;
}

.contacts__text {
  margin: 30px 0 25px;
  padding: 0;
  width: 50%;
  box-sizing: border-box;
  &--plea {
    padding-right: 10px;
    font-size: 16px;
    line-height: 20px;
    text-transform: uppercase;
  }
  &--adress {
    display: flex;
    flex-direction: column;
    @include par;
    line-height: 20px;
  }
}


@media (min-width: $tablet) {
  .contacts__text-block {
      padding: 0 70px;
  }

  .contacts__text {
    margin: 64px 0 55px;
    width: 30%;
    box-sizing: border-box;
    &--plea {
      font-size: 20px;
      line-height: 25px;

    }
    &--adress {
      padding-right: 2px;
      align-items: flex-end;

      text-align: right;
      font-size: 16px;
      span {
        margin-top: 20px;
      }
    }
  }
}

@media (min-width: $desktop) {
  .contacts {
    position: relative;
  }

  .contacts__text-block {
    position: absolute;
    z-index: 1;
    top: 95px;
    left: $padding;

    padding: 0;
    width: 39.6%;

    background-color: #ffffff;
  }

  .contacts__text {
    width: 50%;
    &--plea {
      padding-left: 13.33%
    }
    &--adress {
      padding: 0;
      padding-left: 7%;
      padding-right: 13.33%;
      align-items: flex-start;

      text-align: left;
    }
  }
}
